import React from 'react';
import { ReactComponent as SVGContact} from './portfolioContact.svg';


class ContactView extends React.Component{
    constructor(props){
        super(props);

        this.state = {
            
        }
    }
    
  
    render(){
        return(
            <div class="contactPageCard" id="card4">
                <div class="svgWrapper">
                    <SVGContact></SVGContact>
                </div>
                <div class="textWrapper"><p id="field5" class="contentText textField5"></p></div>
                <div class="textWrapper"><p id="field6" class="contentText textField6"></p></div>

                <div class="inputContainer">
                    <input class="subjectInput" required></input>
                    <div class="inputDescription">Subject</div>
                    <div class="inputUnderline"></div>
                </div>
                <div class="messageContainer">
                    <textarea placeholder="Type your message here" class="messageInput"></textarea>
                    <div class="textareaWrapper"></div>
                </div>
                <div class="sendBtnContainer">
                    <button class="sendButton">Send</button>
                </div>
            </div>
        );
    }
}
  
export default ContactView;